<template>
  <!-- 비디오 section -->
  <section class="first_section">
    <div class="vision_con">
      <div class="vision_text" style="display: none">
        <span class="vision_title fir_coment">SIMPLE</span>
        <span class="vision_title third_coment">IS</span>
        <span class="vision_title second_coment">THE</span>
        <span class="vision_title fourth_coment">BEST</span>
      </div>
      <div class="vision_text_mobile">
        <span class="vision_title fir_coment">SIMPLE IS</span>
        <span class="vision_title second_coment">THE BEST</span>
      </div>
      <div class="vision_dec vision_text">
        <p>‘위대한 진리는 단순하고 소박하다.’라고 했던 톨스토이의 말처럼</p>
        <p>
          무언가를 넘치도록 하기보다 핵심을 편하고 간결하게 필요한 것으로
          전달하기 위함입니다.
        </p>
      </div>
    </div>
    <div class="scroll_con">
      <div class="box">
        <v-icon class="down_icon">mdi-chevron-down</v-icon>
        <!-- <div class="scroll_line">
              <div>
                <span class="line-1"></span>
                <span class="line-2"></span>
              </div>
            </div> -->
        <p class="scroll_text">Scroll</p>
      </div>
    </div>
  </section>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: "first_section",
  data() {
    return {};
  },
  computed: {
    ...mapGetters(["isScrolling"]),
  },
  mounted() {
    this.animateTitle();
    this.animateComments();
    this.animateDec();
    this.animateScrollOpacity();
    this.animateScroll();
    this.setupScrollToNextSection();
  },
  methods: {
    ...mapActions(["setIsScrolling"]),
    scrollHandler() {
      this.setIsScrolling(true);
    },
    animateTitle() {
      this.$anime({
        targets: ".vision_title",
        duration: 800,
        delay: 0,
        opacity: 1,
        easing: "easeInSine",
      });
    },
    animateComments() {
      const comments = [
        ".fir_coment",
        ".second_coment",
        ".third_coment",
        ".fourth_coment",
      ];
      const durations = [1500, 1500, 1500, 1000];
      const delays = [1500, 2250, 3000, 3500];

      comments.forEach((comment, index) => {
        this.$anime({
          targets: comment,
          color: "rgba(255, 255, 255, 1)",
          delay: delays[index],
          duration: durations[index],
          easing: "easeInSine",
        });
      });

      this.$anime({
        targets: comments,
        margin: ["0% 1vw"],
        top: "-5vh",
        delay: 3600,
        duration: 2000,
        easing: "easeInOutQuad",
      });
    },
    animateDec() {
      this.$anime({
        targets: ".vision_dec",
        opacity: 1,
        top: "-5vh",
        delay: 5000,
        duration: 1000,
        easing: "easeInOutQuad",
      });
    },
    animateScrollOpacity() {
      this.$anime({
        targets: ".scroll_con",
        opacity: 1,
        delay: 5500,
        duration: 1000,
        easing: "easeInOutQuad",
      });
    },
    animateScroll() {
      this.$anime({
        targets: ".down_icon",
        bottom: [-15, -5],
        duration: 600,
        easing: "easeInOutQuad",
        direction: "alternate",
        loop: true,
      });
    },

    setupScrollToNextSection() {
      const section = document.querySelector(".first_section");
      let isTouching = false;

      section.addEventListener(
        "touchstart",
        () => {
          isTouching = true;
        },
        { passive: true }
      );

      section.addEventListener(
        "touchmove",
        () => {
          isTouching = true;
        },
        { passive: true }
      );

      section.addEventListener(
        "touchend",
        () => {
          isTouching = false;
        },
        { passive: true }
      );

      section.addEventListener(
        "wheel",
        (e) => {
          if (!isTouching) {
            e.preventDefault();
            if (this.isScrolling) {
              return;
            }
            this.setIsScrolling(true);

            if (e.deltaY > 0 && this.isScrolling) {
              window.scrollTo({
                top: window.innerHeight,
                behavior: "smooth",
              });
            } else if (e.deltaY < 0 && this.isScrolling) {
              window.scrollTo({
                top: 0,
                behavior: "smooth",
              });
            }
            setTimeout(() => {
              this.setIsScrolling(false);
            }, 500);
          }
        },
        { passive: false }
      );
    },
  },
};
</script>

<style lang="scss" scoped>
@media screen and (min-width: 1080px) {
  .vision_title {
    font-size: 6.25rem;
  }
  .vision_dec {
    font-size: 1.375rem;
  }
  .scroll_text {
    font-size: 1.25rem;
  }
  .down_icon {
    font-size: 3rem;
  }
}
// 노트북
@media screen and (min-width: 760px) and (max-width: 1080px) {
  .vision_title {
    font-size: 4.625rem;
  }
  .detail_dec {
    font-size: 1rem;
  }

  .scroll_text {
    font-size: 1rem;
  }
  .down_icon {
    font-size: 2rem;
  }
}
// 태블릿
@media screen and (min-width: 640px) and (max-width: 759px) {
  .vision_title {
    font-size: 3.125rem;
  }
  .vision_dec {
    font-size: 0.89rem;
  }

  .scroll_text {
    font-size: 0.89rem;
  }
  .down_icon {
    font-size: 1.625rem;
  }
}
// 휴대폰
@media screen and (min-width: 320px) and (max-width: 639px) {
  .vision_title {
    font-size: 52.39px;
  }
  .vision_dec {
    font-size: 0px;
  }

  .scroll_text {
    font-size: 10px;
  }
  .down_icon {
    font-size: 16px;
  }
  .vision_con {
    width: 80%;
    .vision_text_mobile {
      color: white;
      .fir_coment {
        justify-content: left;
        display: flex;
        margin: 0% 5vw;
      }
      .second_coment {
        justify-content: right;
        display: flex;
        margin: 0% 5vw;
      }
    }
  }
}

/* 메인 Video */

.first_section {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-image: url("../../assets/intro/rllast_bg.svg");
  background-size: cover;
  background-position: center;
  z-index: -1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.vision_con {
}
.vision_text_mobile,
.vision_text {
  color: rgba(255, 255, 255, 0);
  justify-content: center;
  text-align: center;
}

.vision_title {
  opacity: 0;
  font-family: sans-serif;
  font-style: italic;
  font-weight: bold;
  position: relative;
}
.vision_dec {
  opacity: 0;
  color: rgb(255, 255, 255);
  font-family: "Pretendard-Regular";
  position: relative;
}

.scroll_con {
  width: 100vw;
  position: absolute;
  display: grid;
  justify-content: center;
  bottom: 0;
  left: 0;
  margin-bottom: 3%;
  opacity: 0;
  .box {
    width: 50px;
    height: 80px;
    display: grid;
    justify-content: center;
    align-items: center;
  }
}
.down_icon {
  color: white;
  width: 100%;
  height: 100%;
  font-size: 15px;
}
.scroll_text {
  position: relative;
  display: flex;
  justify-content: center;
  color: white;
  font-size: 10px;
}
</style>
