          <div>
            <span class="service_sub_title"
              ><span style="box-shadow: #0174f5 0px 2px 0px 0px"
                >Service</span
              ></span
            >
            <p class="service_sub_subtitle">현재 서비스를 제공하고 있습니다.</p>
          </div>
          <span class="service_sub_detail" style="display: none">
            <p>
              고객을 위한 웹/앱 맞춤 설계, 유지보수 서비스 제공 다양한 플랫폼과
              새로운 기술로
            </p>
            빠르게 변화하는 환경에 능동적으로 대응하여, 커뮤니케이션을 통해
            만족감을 총족시켜드립니다.
          </span>
        </div>
      </v-row>
      <v-row class="row_con" style="height: 65vh">
        <div class="card_wrap">
          <div class="webapp_img">
            <div class="hover_img"></div>
            <div class="card_text">
              <p class="card_title">Web / APP</p>
              <p class="card_title_2">Development</p>
              <p class="card_desc">Web/App A to Z 개발</p>
            </div>
            <router-link to="/service">
              <div class="web_logo_btn">
                <img
                  src="@/assets/home/service_arrow.svg"
                  style="
                    opacity: 1;
                    transition: 1s opacity ease;
                    position: absolute;
                  "
                  class="arrow1"
                />
                <img
                  src="@/assets/home/service_arrow_hover.svg"
                  class="arrow_hover1"
                  style="
                    opacity: 0;
                    transition: 1s opacity ease;
                    position: absolute;
                  "
                />

                <!-- <span class="web-line1"></span>
                <span class="web-line2"></span> -->
              </div>
            </router-link>
          </div>
          <div class="platform_img">
            <div class="hover_img"></div>
            <div class="card_text_hover">
              <p class="card_title">SERVICE</p>
              <p class="card_title_2">PLATFORM</p>
              <p class="card_desc">플랫폼 A to Z 운영</p>
            </div>
            <router-link to="/service">
              <div class="platform_logo_btn">
                <img
                  src="@/assets/home/service_arrow.svg"
                  style="
                    opacity: 1;
                    transition: 1s opacity ease;
                    position: absolute;
                  "
                  class="arrow2"
                />
                <img
                  src="@/assets/home/service_arrow_hover.svg"
                  class="arrow_hover2"
                  style="
                    opacity: 0;
                    transition: 1s opacity ease;
                    position: absolute;
                  "
                />
                <!-- <span class="platform-line1"></span>
                <span class="platform-line2"></span> -->
              </div>
            </router-link>
