<template>
  <section class="slide_section">
    <div class="section_container">
      <div class="slide_span_con">
        <div class="slide_span">
          <span class="span_title">Now we are</span>
          <span class="span_desc">게임인스 소식을 전해드립니다.</span>
          <div class="btn_more">
            <router-link to="/news" class="span_btn"> View More </router-link>
          </div>
        </div>
        <div class="slide_box">
          <div class="slide_con">
            <div
              style="padding: 12px"
              v-for="slide in slides"
              :key="slide.id"
              class="slideimg"
            >
              <router-link to="/news">
                <img
                  :src="slide.image"
                  alt="Slide Image"
                  :class="'slide' + slide.id"
                />
              </router-link>
            </div>
            <div
              class="clone"
              style="padding: 12px"
              v-for="slide in slides"
              :key="slide.id"
            >
              <router-link to="/news">
                <img
                  :src="slide.image"
                  alt="Slide Image"
                  :class="'slide' + slide.id"
                />
              </router-link>
            </div>
          </div>

          <!-- 스와이프 -->
          <div class="swiper-container2">
            <div class="swiper-wrapper">
              <div
                v-for="(imgs, index) in slides"
                :key="index"
                class="swiper-slide"
              >
                <div class="swipe-con">
                  <router-link to="/news">
                    <img
                      :src="imgs.image"
                      alt="Slide Image"
                      class="swipe-img"
                    />
                  </router-link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import SwiperCore from "swiper";
import { ref } from "vue";

import store from "@/store";
import { mapGetters } from "vuex";
export default {
  name: "slide_section",
  computed: {
    ...mapGetters(["isScrolling"]),
  },
  data() {
    return {
      slides: [
        {
          id: 1,
          image: require("@/assets/sub/Mask group.svg"),
        },
        {
          id: 2,
          image: require("@/assets/sub/Mask group-1.svg"),
        },
        {
          id: 3,
          image: require("@/assets/sub/Mask group-2.svg"),
        },
        {
          id: 4,
          image: require("@/assets/sub/Mask group-3.svg"),
        },
        {
          id: 5,
          image: require("@/assets/sub/Mask group-4.svg"),
        },
        {
          id: 6,
          image: require("@/assets/sub/Mask group-5.svg"),
        },
        {
          id: 7,
          image: require("@/assets/sub/Mask group-6.svg"),
        },
        {
          id: 8,
          image: require("@/assets/sub/Mask group-7.svg"),
        },
        {
          id: 9,
          image: require("@/assets/sub/Mask group-8.svg"),
        },
        {
          id: 10,
          image: require("@/assets/sub/Mask group-9.svg"),
        },
      ],
    };
  },
  mounted() {
    const swiperslide = ref(null);
    swiperslide.value = new SwiperCore(".swiper-container2", {
      direction: "horizontal",
      loop: true,
      mousewheel: false,
      slidesPerView: 1,
      speed: 1000,
      autoplay: {
        delay: 1000,
        disableOnInteraction: false,
      },
      touch: true,
      simulateTouch: true,
      touchEventsTarget: "container",
    });

    const wrapper = document.querySelector(".slide_section");
    const contact_section = document.querySelector(".contact_section");

    var max_Scroll = document.documentElement.scrollHeight - window.innerHeight;

    wrapper.addEventListener("wheel", function (e) {
      e.preventDefault();

      if (store.getters.isScrolling) {
        return;
      }
      store.dispatch("setIsScrolling", true);
      if (e.deltaY < 0) {
        if (window.scrollY === max_Scroll) {
          window.scrollTo({
            top: window.scrollY - contact_section.scrollHeight,
            behavior: "smooth",
          });
        } else {
          window.scrollTo({
            top: window.scrollY - window.innerHeight,
            behavior: "smooth",
          });
        }
      } else if (e.deltaY > 0) {
        window.scrollTo({
          top: window.scrollY + window.innerHeight,
          behavior: "smooth",
        });
      }
      setTimeout(() => {
        store.dispatch("setIsScrolling", false); // 스크롤 종료시 isScrolling 값을 false로 변경
      }, 500); // 1초 후에 변경되도록 설정
    });
  },
};
</script>

<style lang="scss" scoped>
