        left: 10%;
      }
    }
  }
  .swiper-container2 {
    display: none;
  }
  .span_title {
    font-size: 5rem;
  }
  .span_desc {
    font-size: 1.25rem;
    margin: 28px 0px 36px 0px;
  }
  .span_btn {
    font-size: 1.25rem;
    width: 180px;
    height: 50px;
    border-radius: 25px;
  }

  @for $i from 0 through 10 {
    .slide#{$i} {
      width: 340px;
