    display: none;
  }
}
// 태블릿
@media screen and (min-width: 640px) and (max-width: 759px) {
  .section_container {
    width: 67%;
    .slide_span_con {
      display: flex;
      width: 115%;
      .slide_box {
        left: 10%;
      }
    }
  }
  .span_title {
    font-size: 2.5rem;
  }
  .span_desc {
    font-size: 0.89rem;
    margin: 28px 0px 36px 0px;
  }
  .span_btn {
    font-size: 0.725rem;
    width: 100px;
