  }
  .btn_more {
    padding-bottom: 40px;
  }
  .span_btn {
    font-size: 14px;
    width: 100px;
    height: 30px;
    align-items: center;
    display: grid;
  }

  .swiper-container2 {
    display: flex;
    .swipe-con {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;
    }
    .swipe-con a {
      display: flex;
      justify-content: center;
      width: 100%;
      height: 100%;
    }
    .swipe-img {
      width: 320px;
      height: 320px;
    }
  }
}

// 기본 css
.slide_section {
  width: 100vw;
  height: 100vh;
  background-color: white;
}

.section_container {
  position: relative;
  height: 100vh;
  margin: auto;

  .slide_span_con {
    justify-content: center;
    align-items: center;
    height: 100%;
  }
}

.slide_box {
  width: 100%;
  position: relative;
  overflow: hidden;
  .slide_con {
    display: flex;
    flex-wrap: nowrap;
    top: 5%;
    position: relative;
    width: 63%;
    animation: bannermove 40s linear infinite;

    .slideimg {
    }
    .clone {
    }
  }
}

.slide_span {
  width: 400px;
  padding: 12px;
  box-shadow: 0px 0px 1px 1px rgb(255, 255, 255);
  transition: all 0.5s ease;
}

.span_title {
  font-family: "Pretendard-Regular";
  font-weight: bold;
  line-height: 60px;
}

.span_desc {
  display: grid;
  font-family: "Pretendard-Regular";
  font-weight: bold;
  letter-spacing: 0px;
}

.btn_more {
  display: flex;
  justify-content: center;
  align-items: center;
}
.span_btn {
  font-family: "Pretendard-Regular";
  border-radius: 6px;
  box-shadow: none;
  border: 1px solid;
  transition: all 0.1s ease-in-out;
}
.span_btn:hover {
  color: #0174f5;
}

@for $i from 0 through 10 {
  .slide#{$i} {
    transition: all 0.5s ease;
  }
  .slide#{$i}:hover {
    transform: translatey(-20px);
  }
}

@keyframes bannermove {
  0% {
    transform: translate(0, 0);
  }

  100% {
    transform: translate(-435%, 0);
  }
}
a {
  text-decoration: none;
  color: black;
}
</style>
