<template>
  <!-- 콘텍트 section -->
  <section class="contact_section">
    <!-- <div class="contact_bg" /> -->
    <div class="contact_con">
      <div class="box">
        <span class="title"
          ><p>게임인스와 함께 멈추지 않는</p>
          <p>성장을 경험하세요.</p></span
        >
        <span class="desc">우리와 프로젝트를 함께 하시겠습니까?</span>
        <div class="btn_con">
          <router-link to="/Contact">
            <img class="btn" src="./../../assets/home/togo_icon.svg" />
          </router-link>
        </div>
      </div>
    </div>
    <Footer />
  </section>
</template>

<script>
import Footer from "./../footer/Footer_Home.vue";
import { mapGetters } from "vuex";
import store from "@/store";

export default {
  name: "contact_section",
  components: {
    Footer,
  },
  computed: {
    ...mapGetters(["isScrolling"]),
  },
  data() {
    return {};
  },
  mounted() {
    const contact_con = document.querySelector(".contact_con");
    const contact_section = document.querySelector(".contact_section");

    var max_Scroll = document.documentElement.scrollHeight - window.innerHeight;

    contact_con.addEventListener("wheel", function (e) {
      e.preventDefault();
      if (store.getters.isScrolling) {
        return;
      }

      store.dispatch("setIsScrolling", true);
      if (e.deltaY < 0) {
        if (window.scrollY === max_Scroll) {
          window.scrollTo({
            top: window.scrollY - contact_section.scrollHeight,
            behavior: "smooth",
          });
        } else {
          window.scrollTo({
            top: window.scrollY - contact_con.scrollHeight,
            behavior: "smooth",
          });
        }
      } else if (e.deltaY > 0) {
        window.scrollTo({
          top: window.scrollY + contact_con.scrollHeight,
          behavior: "smooth",
        });
      }
      setTimeout(() => {
        store.dispatch("setIsScrolling", false);
      }, 500);
    });
  },
};
</script>

<style lang="scss" scoped>
@media screen and (min-width: 1080px) {
  .box {
    width: 45%;
    margin: 0% 17%;
  }
  .title {
    font-size: 2.5rem;
  }

  .desc {
    font-size: 1.25rem;
  }

  .btn_con {
    justify-content: left;
  }
  .btn {
    cursor: pointer;
    width: 4.125rem;
    height: 75%;
  }
}
// 노트북
@media screen and (min-width: 760px) and (max-width: 1080px) {
  .box {
    width: 45%;
    margin: 0% 17%;
  }
  .title {
    font-size: 1.3rem;
  }

  .desc {
    font-size: 0.89rem;
  }

  .btn_con {
    justify-content: left;
  }
  .btn {
    cursor: pointer;
    width: 3.25rem;
    height: 75%;
  }
}
// 태블릿
@media screen and (min-width: 640px) and (max-width: 759px) {
  .box {
    width: 45%;
    margin: 0% 17%;
  }
  .title {
    font-size: 1.125rem;
  }

  .desc {
    font-size: 0.7rem;
  }
  .btn_con {
    justify-content: left;
  }
  .btn {
    cursor: pointer;
    width: 2.5rem;
    height: 75%;
  }
}
// 휴대폰
@media screen and (min-width: 320px) and (max-width: 639px) {
  .box {
    width: 100%;
    text-align: center;
  }
  .title {
    font-size: 20px;
  }

  .desc {
    font-size: 16px;
  }

  .btn_con {
    justify-content: center;
    padding-bottom: 7%;
  }
  .btn {
    cursor: pointer;
    width: 36px;
  }
}

.contact_section {
  position: relative;
  width: 100vw;
  height: 40vh;
}

.contact_bg {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.contact_con {
  background-size: cover;
  background-image: url("./../../assets/home/contact_img.svg");
  .box {
    display: grid;
    height: 100%;
    color: white;
  }
}

.title {
  font-family: "Pretendard-Regular";
  font-weight: bold;
  padding-top: 13%;
}

.desc {
  font-family: "Pretendard-Regular";
  margin: 5% 0%;
}

.btn_con {
  display: flex;
  width: 100%;
}

.btn {
  cursor: pointer;
  padding-bottom: 5%;
  height: 75%;
}
</style>
